<template>
    <section>
        <tg-list ModuleName="Ticketing" SubModuleCode="FSM_WorkOrder" IdentityColumn="TICKET_ID" :listType="['Card']"
            :ListData="GetWorkOrderListing" :HeaderData="Headers" :TotalRecords="TotalRecords" :IsShowAction="true"
            :RenderRowActionMethod="workOrderRenderActions" :ListDataCallBackFunction="FetchRecords"
            :NorecordfoundText="$t('NoRecordfound')" v-on:ActionButtonClick="workorderActionButtonClick"
            v-on:PagerButtonClick="pagerMethod" v-on:SortdataButtonClick="sortdata">
            <template v-slot:customcard="{ data }">
                <div v-bind:class="{'row card-view bg-border custom-box-width':true}">
                    <div v-bind:class="{'col-md-12 mb-3':true, 'dg-danger':data.row.is_high_profile == 'YES' || data.row.is_high_profile == 'true'}">
                        <div class="card-box blue-bg-light">
                            <div class="card-number">
                                <div class="card-number" v-if="data.row.sa_status != ''">
                                    #{{ data.row.workorder_number }} ({{  data.row.status_id }}-{{ data.row.sa_status }})
                                </div>
                                <div class="card-number" v-else>
                                    #{{ data.row.workorder_number }} ({{  data.row.status_id }})
                                </div>
                            </div>                            
                            <div class="card-detail" v-if="(data.row.subject != '' && data.row.subject != null)"><em class="fa fa-file-text" aria-hidden="true"></em><span>{{data.row.subject}}</span></div>
                            <div class="card-detail" v-if="(data.row.worktype_name != '' && data.row.worktype_name != null)"><em class="fa fa-info-circle" aria-hidden="true"></em><span data-toggle-tooltip="tooltip" data-original-title="Work Type" style="width:auto">{{data.row.worktype_name}}</span></div>
                            <div class="card-detail" v-if="(data.row.item_name != '' && data.row.item_name != null)"><em class="fa fa-file" aria-hidden="true"></em> <span>{{data.row.item_name}}</span></div>
                            <div class="card-detail" v-if="(data.row.created_by != '' && data.row.created_by != null)"><em class="fa fa-user" aria-hidden="true"></em><span>{{data.row.created_by}}</span></div>
                            <hr class="my-2">
                            <p>
                                <span v-if="(data.row.child_sub_status_name != '' && data.row.child_sub_status_name != null)" v-bind:title="data.row.child_sub_status_name">{{data.row.child_sub_status_name}}</span>
                                <span v-if="(data.row.comment != '' && data.row.comment != null)" class="description mt-2" v-bind:title="data.row.comment"><br /> {{data.row.comment}}</span>
                                <span v-if="(data.row.zone_name != '' && data.row.zone_name != null)" class="description mt-2" v-bind:title="data.row.zone_name"><br /> <b>{{$t('HighProfileZoneName')}}:</b> {{data.row.zone_name}}</span>
                                <span class="description mt-2" v-bind:title="data.row.is_submited"><br />{{ data.row.is_submited }}</span>
                            </p>
                            <div class="bottom-action">
                                <div class="card-date"><em class="fa fa-calendar-o" aria-hidden="true"></em> <span>{{$options.filters.formatDateTime(data.row.work_order_date) }}</span></div>
                                <div class="card-action">
                                    <a title="ReportIncident" v-on:click="openReportIncidentListingPopUp(data.row)" href="javascript:;" class="round-icon-small btn-primary-light d-none"><em class="fa fa-file-text-o" aria-hidden="true"></em></a>
                                    <a title="Update Status"  v-on:click="openPopupForUpdateStatus(data.row)" href="javascript:;" class="round-icon-small btn-success-light"><em class="fa fa-cog"></em></a>
                                    <a v-on:click="openUpdateStatusHistoryPopUp(data.row)" title="View Status History" href="javascript:;" class="round-icon-small btn-info-light  d-none"><em class="fa fa-history"></em></a>
                                    <a title="View TrackHistory" v-on:click="openTrackHistorModal(data.row)" href="javascript:;" class="round-icon-small btn-primary-light d-none"><em class="fa fa-eye"></em></a>
                                    <a class="round-icon-small btn-primary-light"  data-toggle-tooltip="tooltip" data-original-title="Detail" @click="WordOrderDetailLink(data.row)" ><em class="fa fa-ticket"></em><span class="text">Detail</span></a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>              
            </template>
        </tg-list>
        <WorkOrderStatusModal v-if="showWorkOrderStatusModal" :updateWorkOrderStatus="updateWorkOrderStatus"
            :isLocateorNonLocate="isLocateorNonLocate" :callbackfunction="callbackfunction"></WorkOrderStatusModal>
        <transition name="modal">
            <div class="my-popups">
                <div class="modal d-block mail-box-pop dialog" v-if="workOrderModalStatus">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title">{{ $t('WorkOrder') + ' ' + $t('Status') }}</h4>
                                <button type="button" class="close" v-on:click="Close">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <dynamic-form lang="en" :schema="nonLocateFormSchema">
                                    <template v-slot:tgslot-Description="{ data }">
                                        {{ data.fieldInfo }}
                                    </template>
                                </dynamic-form>
                            </div>
                            <div class="modal-footer">
                                <a tabindex="13" class="btn btn-success formbtn text-white" :title="$t('Save')"
                                    v-on:click="SubmitWOrkOrderUpdateStatus" id="btnSave"><i class="fa fa-save pr-2"></i>
                                    {{ $t('Save') }}</a>
                                <a tabindex="14" v-on:click="Close" :title="$t('Cancel')" id="btnCancel"
                                    class="btn btn-danger"><i class="fa fa-times"></i> {{ $t('Cancel') }}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>
<script>

import DataServices from "@/services/DataService";
import WorkOrderStatusModal from "@/components/Modal/WorkOrderStatusModal.vue";
export default {
    components: { WorkOrderStatusModal },
    props: {
        ticketId: {
            type: Number,
            required: false
        },
        isLocateorNonLocate: {
            type: String,
            required: true
        },
        callbackfunction: {
            type: Function
        }        
    },
    data() {
        return {
            noRecords: false,
            TotalRecords: 0,
            CurrentPage: 1,
            TotalPages: 0,
            listActions:[],
            nonLocateFormSchema: [
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "SelectField",
                            mode: "tag",
                            label: this.$t('Status'),
                            name: "Status",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                }
            ],
            Headers: [{
                "COLUMN_NAME": "workorder_number",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "WO Number",
                "DISPLAY_ORDER": 1
            },
            {
                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 2
            },
            {
                "COLUMN_NAME": "work_order_date",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Order Date",
                "DISPLAY_ORDER": 3
            },
            {
                "COLUMN_NAME": "worktype_name",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Work Type",
                "DISPLAY_ORDER": 4
            },
            {
                "COLUMN_NAME": "subject",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Subject",
                "DISPLAY_ORDER": 5
            },
            {
                "COLUMN_NAME": "client_id",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Client",
                "DISPLAY_ORDER": 6
            },
            {
                "COLUMN_NAME": "is_submited",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "FormSubmitted",
                "DISPLAY_ORDER": 7
            }],
            SortBy: '',
            SortExp: '',
            PageSize: "10",
            PageNumber: "1",
            GroupStart: 1,
            workOrderId: 0,
            GroupEnd: 3,
            GetWorkOrderListing: [],
            showWorkOrderStatusModal: false,
            updateWorkOrderStatus: [],
            workOrderModalStatus: false            
        }
    },
    created: async function () {        
        var vm = this;       
        vm.FetchRecords();     
    },
    methods: {
        GetDropDownData: async function (type) {
            var vm = this;
            var param = `id=&moduleName=Ticketing&type=${type}&search=&refId=`;
            vm.workorderStatusDataList = [];
            await DataServices.GetDDLData(param).then(function (response) {
                if (response.data.DATA.length > 0) {
                    var json = response.data.DATA;
                    vm.workorderStatusDataList = response.data.DATA;
                    if (json != null) {
                        response.data.DATA.forEach((item, index) => {
                            vm._data.nonLocateFormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        FetchRecords: async function () {
            //debugger
            var vueObj = this;
            vueObj.isLoading=true;
            var url = `ticketId=${vueObj.ticketId}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&pageSize=${vueObj.PageSize}&pageNumber=${vueObj.PageNumber}&lang=EN`;
            await DataServices.WorkOrderList(url).then(function (response) {
                if (response.data != "" && response.data != null) {
                    if (response.data.result.data.length > 0) {
                        vueObj.GetWorkOrderListing = response.data.result.data;
                        vueObj.TotalRecords = response.data.result.data[0].TOTALRECORDS;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.IsLoad = true;
                    } else {
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0
                        vueObj.GetWorkOrderListing = [];
                    }
                    vueObj.isLoading=false;
                }
                else {
                    vueObj.noRecord = true;
                    vueObj.TotalRecords = 0
                    vueObj.GetWorkOrderListing = [];
                }        
                vueObj.isLoading=false;       
                setTimeout(function () { vueObj.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecords();
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.FetchRecords();
        },
        workorderActionButtonClick(callbackfunction, item, index, event) {
            //debugger;
            switch (callbackfunction) {
                case 'Update':
                    this.openPopupForUpdateStatus(item);
                    break;
                case 'ClickOnDetail':
                    this.WordOrderDetailLink(item);
                    break;
                case 'Status':
                    this.WorkOrderStatus(item);
                    break;
            }
        },
        WordOrderDetailLink: function (item) {
            var vm = this;
            vm.$router.push({ path: "/Ticketing/WorkOrderDetail/" + item.id + "/" + item.ticket_id + "" });
        },
        SubmitWOrkOrderUpdateStatus: async function () {
            //debugger;
            var vm = this;
            var jsonobj =
            {
                Id: vm.workOrderId,
                StatusId: vm._data.nonLocateFormSchema[0].Data[0].value
            }
            var params = JSON.stringify(jsonobj);
            await DataServices.MarkAsDone(params).then(function (response) {
                //debugger;
                if (response.data == 1) {
                    vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Status")]), "success", true, vm.$t("Alert"));
                    vm.Close();
                    vm.FetchRecords();
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Status")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        },
        Close: function () {
            this.workOrderModalStatus = false;
        },
        WorkOrderStatus: function (item) {
            //debugger;
            var vm = this;
            vm.workOrderId = item.id;
            vm.GetDropDownData('WORK_ORDER_FINAL_STATUS');
            vm.workOrderModalStatus = !vm.workOrderModalStatus;
        },
        openPopupForUpdateStatus: function (item) {
            var vm = this;
            vm.updateWorkOrderStatus = item;
            vm.showWorkOrderStatusModal = !vm.showWorkOrderStatusModal;
        },
        workOrderRenderActions: function (row) {
            ////debugger;
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Update':
                        action.isVisible = true;
                        break;
                    case 'Details':
                        action.isVisible = true;
                        break;
                    case 'Status':
                        action.isVisible = (self.isLocateorNonLocate == true);
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        }

    },
}
</script>