<template>
    <transition name="modal">
        <div class="my-popups">
            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title"> {{ $t('FollowUp') }}</h5>
                            <button v-on:click="closePopup()" type="button" class="close"><em class="fa fa-times"
                                    aria-hidden="true"></em></button>
                        </div>
                        <div class="modal-body" style="max-height:600px;overflow-y:auto;">
                            <div class="listing">
                                <div class="table-responsive" v-if="followupList.length > 0">
                                    <table class="table table-bordered dt-responsive nowrap mt-0"
                                        id="table-openactivities-content" aria-describedby="folowuplist">
                                        <thead class="thead-bg">
                                            <tr>
                                                <th scope="col"><span> {{ $t('ScheduleDate') }} </span></th>
                                                <th scope="col"><span> {{ $t('Created BY') }}</span></th>
                                                <th scope="col"><span> {{ $t('Comments') }} </span></th>
                                                <th scope="col" class="text-center"><span> {{ $t('Action') }} </span></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr v-for="(item, index) in followupList" :key="index">
                                                <!-- <td><a href="javascript:;"  v-on:click="openToAddEditFollowUp(item);">{{item.followup_notification_subject}}</a></td> -->
                                                <td>{{ SetScheduleDateTime(item.scheduleDate, item.time) }}</td>
                                                <td>{{ item.createdBy }}</td>
                                                <td>{{ item.followUpDescription }}</td>
                                                <td class="text-center single-action"><a href="javascript:;"
                                                        :title="$t('Edit')"
                                                        v-on:click="openToAddEditFollowUp(item);"  :class="{ 'unclickable': !showEditButton(item) }"><em
                                                            class="fa fa-pencil text-success"></em></a></td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                                <div v-if="followupList.length == 0" class="text-center text-danger p-4 border no-record">
                                    {{ $t('NoRecordfound') }}</div>
                            </div>
                        </div>
                        <div class="modal-footer">
                            <div class="col-lg-6 text-right p-0">
                                <a href="javascript:;" class="btn btn-success formbtn text-white"
                                    v-on:click="openToAddEditFollowUp(null)"><em class="fa fa-plus pr-2"></em>{{ $t('Add') }}
                                    {{ $t('FollowUp') }}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <modaladdfollowup v-if="showAddEventModalForAddEdit" ref="addEditFollowupModal"
                v-on:close="showAddEventModalForAddEdit = false" :followupListById="followupListById"
                :followUpId="followUpId" :sourceid="sourceId" :ticketStatus="statustype">
            </modaladdfollowup>
        </div>
    </transition>
</template>
<script>
import DataService from "../../services/DataService";
import modaladdfollowup from "./modalfollowupevent.vue"
export default {
    components: {
        modaladdfollowup
    },
    props: {
        ticketStatus: {
            type: String,
            required: true
        },
        ticketId: {
            Type: String,
            required: true
        },
        userName:{
             type:String,
             required:true
        }
    },
    data: function () {
        return {
            followupList: [],
            sourceId: null,
            listItem: null,
            statustype: null,
            result: false,
            showAddEventModalForAddEdit: false,
            followupListById: [],
            followUpId: null
        }
    },
    created: function () {
        this.sourceId = this.ticketId;
        this.loadFollowUpData(this.ticketId, this.ticketStatus);
    },
    methods: {
        SetScheduleDateTime: function (scheduleDate, time) {
            var vm = this;
            var scheduleDateTime = '';
            var hours = parseInt(time.split(':')[0]);
            var minutes = parseInt(time.split(':')[1]);
            var amOrPm = hours >= 12 ? 'PM' : 'AM';
            hours = hours % 12;
            hours = hours ? hours : 12; 
            var formattedHours = hours < 10 ? '0' + hours : hours;
            var formattedMinutes = minutes < 10 ? '0' + minutes : minutes;
            
            scheduleDateTime = scheduleDate + ' ' + formattedHours + ':' + formattedMinutes;
            scheduleDateTime += ' ' + amOrPm;
            return scheduleDateTime;
        },
        showEditButton: function (item) {
            //debugger
            var vm = this;
            var finalresult;
            // Assuming created_by is the field containing the user_id of the creator
            // You might need to adjust this based on your actual data structure           
            // vm.result =  item.createdBy === this.userName ? true :  false; // Replace loggedInUserId with the user_id you want to check     
             if(item.createdBy===vm.userName){
                finalresult=true
             }
             else{
                finalresult=false;
             }
             return finalresult;        
        },
        loadFollowUpData: function (id, statustype) {
            //debugger;
            if (typeof id != "undefined" && id != null && id != "") {
                var vm = this;
                vm.statustype = statustype;
                vm.sourceId = id;
                vm.followupList = [];
                var params = `ticketId=${vm.sourceId}&ticketStatus=${vm.statustype}`;
                DataService.FollowupList(params).then(function (response) {
                    //debugger;
                    if (response.data != "" && response.data != null && response.data.length > 0) {
                        vm.followupList = response.data;
                    } else {
                        vm.followupList = [];
                    }
                });
            }
        },

        closePopup: function () {
            this.$emit('closeFollowup');
        },
        openToAddEditFollowUp: function (item) {
            //debugger;
            var vm = this;
            if (item != null) {
                vm.followUpId = item.followUpId;
                vm.followupListById = item;
            }
            else {
                vm.followUpId = item;
                vm.followupListById = item;
            }
            vm.showAddEventModalForAddEdit = true;
        }

    }
}
</script>


<style scoped>
    .unclickable {
    pointer-events: none;
    cursor: disabled;
    opacity: 0.5; /* You can adjust the opacity to visually indicate the disabled state */
}
</style>

