<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full d-flex">
                        <h4 class="modal-title">{{ $t('AddWorkOrder') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                        <span class="user-guide">
                            <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button"
                                aria-expanded="false" aria-controls="collapseExample"> <em
                                    class="fa fa-files-o pr-2"></em>{{ $t('GuideForUser') }}</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t('UgAddEditWorkOrder')"></div>
                                </div>
                            </div>
                        </span>
                    </div>

                    <div class="modal-body">
                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                            <template v-slot:tgslot-Mapslot="{ data }">
                                <ManageMap class="row" mapType="MapBox" :config="config" mapContainer="WorkOrder"
                                    v-if="isShowMap" :Data="mapObj" @GetAddress="GetAddress" :key="componentKey">
                                </ManageMap>
                            </template>
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
import ManageMap from './../../components/Common/MapComponent.vue';
export default {
    components: { ManageMap },
    props: {
        WorkOrdrId: {
            type: Number,
            required: true
        },
        form: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            config: {
                isShowDrawButton: true,
                isShowEtaOrLocator: true,
                isShowSearchBox: true
            },
            isLoading: false,
            isShowMap: true,
            showSetting: false,
            clientId: 0,
            CompanyType:'',
            WorkOrderNumber: "",
            selectClientvalue: '',
            CountryDDl: '',
            StateDDl: '',
            selectvalue: '',
            Latitude: '',
            Longitude: '',
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "CustomSelectField",
                            label: this.$t('TicketNumber'),
                            name: "TicketNumber",
                            value: "",
                            config: {
                                options: [],
                                onSearchChange: this.onSearch,
                                onChange: this.setSelectedValue
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "WorkOrderTitle",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "DateField",
                            label: this.$t('WorkOrderDate'),
                            name: "WorkOrderDate",
                            value: '',
                            placeholder: "",
                            config: {
                                 format: "M/D/YYYY",
                                 minDate: new Date()
                         },
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('WorkType'),
                            name: "WorkType",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Product'),
                            name: "Product",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Client'),
                            name: "Client",
                            value: "",
                            disabled: true,
                            config: {
                                options: [],
                                onChange: this.GetClientaddress
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('ClientAddress'),
                            name: "ClientAddress",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.fetchAddressByClientId
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Utility'),
                            name: "Utility",
                            value: "",
                            config: {
                                options: [],
                                onChange: this.GetCDCDdlData
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('CDC'),
                            name: "CDC",
                            value: "",
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "single",
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('description'),
                            name: "Description",
                            value: "",
                            placeholder: this.$t('EnterDescription'),
                            validationRules: { "required": true },
                        },
                    ],
                },
                {
                    layoutType: "double",
                    group_name: 'Preferred Time',
                    Data: [
                        {
                            astype: "DateField",
                            label: this.$t('StartDateTime'),
                            name: "StartDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "M/D/YYYY",
                                onChange: this.GetStartData
                            },
                            validationRules: "",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('EndDate'),
                            name: "EndDate",
                            value: '',
                            placeholder: "",
                            config: {
                                format: "M/D/YYYY",
                                minDate: ""
                            },
                            validationRules: "",
                        }
                    ]
                },
                {
                    layoutType: "triple",
                    group_name: 'Address',
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Address'),
                            name: "Address",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Country'),
                            name: "Country",
                            value: "",
                            disabled: true,
                            config: {
                                options: [],
                                onChange: this.GetStateDDL
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('State'),
                            name: "State",
                            value: "",
                            disabled: true,
                            config: {
                                options: []
                            },
                            placeholder: this.$t('Select'),
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('County'),
                            name: "County",
                            value: "",
                            disabled: true,
                            placeholder: "",
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('City'),
                            name: "City",
                            value: "",
                            disabled: true,
                            placeholder: "",
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Street'),
                            name: "Street",
                            value: "",
                            disabled: true,
                            placeholder: "",
                            validationRules: "",
                        },
                        {
                            astype: "TextField",
                            label: this.$t('ZipCode'),
                            name: "ZipCode",
                            value: "",
                            disabled: true,
                            placeholder: "",
                            validationRules: "",
                        },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [

                        {
                            astype: "SlotField",
                            label: "",
                            name: "Mapslot",
                            value: "",
                            placeholder: "",
                            visibility: true,
                            validationRules: "",
                            settings: {
                                isInSlot: true
                            }
                        },
                    ]
                },
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.Close
                }
            ],
        }
    },
    created: async function () {
        var vueObj = this;
        if (vueObj.$parent.TicketId != null && vueObj.$parent.TicketNumber != null) {
            vueObj.tiketidNumber = vueObj.$parent.TicketId;
            let ticketNumberValue = {
                label: vueObj.$parent.TicketNumber,
                value: vueObj.$parent.TicketId

            }
            vueObj.FormSchema[0].Data[0].value = ticketNumberValue;
            vueObj.FormSchema[0].Data[0].disabled = true;
            vueObj.setClientData(null, vueObj.tiketidNumber);
        }
        this.IsExtraMapFeature();
        this.GetDdlRecords_WorkType();
        this.GetProductRecords();
        this.GetAddClientDDL();
        this.GetCountryDDL();
    },
    methods: {
        GetAddress: function (data) {
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            var indexValue = data.length - 1;
            let filteredCountry = vueObj.CountryDDl.filter((item) => {
                return item.name === data[indexValue].CompleteAddress.Country.LongName;
            });
            (vueObj.FormSchema)[3].Data[0].value = data[indexValue].CompleteAddress.FullAddress;
            var filterCountry = filteredCountry == '' ? '' : filteredCountry[0].value;
            (vueObj.FormSchema)[3].Data[1].value = filterCountry;
            vueObj.GetStateDDL(null, filterCountry);
            setTimeout(function () {
                let filteredState = vueObj.StateDDl.filter((item) => {
                    return item.name === data[indexValue].CompleteAddress.AdministrativeAreaLevel1.LongName;
                });
                var filterState = filteredState == '' ? '' : filteredState[0].value;
                (vueObj.FormSchema)[3].Data[2].value = filterState;
            }, 1000);
            (vueObj.FormSchema)[3].Data[3].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel2.LongName;
            (vueObj.FormSchema)[3].Data[4].value = data[indexValue].CompleteAddress.City.LongName;
            (vueObj.FormSchema)[3].Data[5].value = data[indexValue].CompleteAddress.AdministrativeAreaLevel3.LongName;
            (vueObj.FormSchema)[3].Data[6].value = data[indexValue].CompleteAddress.PinCode.LongName;
            var dataLat1 = ''; var dataLat2 = ''; var dataLng1 = ''; var dataLng2 = '';
            data[indexValue].LatLng.forEach((item, index) => {
                if (index == 0) {
                    dataLat1 = item.Lat;
                    dataLat2 = item.Lng;
                }
                else {
                    dataLng1 = item.Lat;
                    dataLng2 = item.Lng;
                }
            });
            vueObj.Latitude = dataLng1 + ',' + dataLat2;
            vueObj.Longitude = dataLat1 + ',' + dataLng2;
        },
        Close: function () {
            this.$parent.AddWorkOrderModal = false;
        },
        IsExtraMapFeature: function () {
            var vueObj = this;
            DataServices.IsExtraMapFeature().then(
                function (response) {
                    vueObj.CompanyType = "";
                    if (response.data != null && response.data != "") {
                        vueObj.CompanyType = response.data;
                        if (vueObj.CompanyType == 'nonlocating' || vueObj.CompanyType == 'normal') {
                            vueObj.FormSchema[0].Data[7].visibility = false;
                            vueObj.FormSchema[0].Data[8].visibility = false;
                            vueObj.FormSchema[0].Data[7].validationRules = { "required": false };
                            vueObj.FormSchema[0].Data[8].validationRules = { "required": false };
                        }
                    } else {
                        vueObj.CompanyType = 'normal';
                        vueObj.FormSchema[0].Data[7].visibility = false;
                        vueObj.FormSchema[0].Data[8].visibility = false;
                        vueObj.FormSchema[0].Data[7].validationRules = { "required": false };
                        vueObj.FormSchema[0].Data[8].validationRules = { "required": false };
                    }
                });
                
        },
        GetStartData: function (event, id) {
            var vueObj = this;
            (vueObj.FormSchema)[2].Data[1].config.minDate = event;
        },
        onSearch: async function (e, selctedValue, search) {
            var vm = this;
            this.searchSelectData(search, this, selctedValue);
        },
        searchSelectData: async function (search, selctedValue) {
            var vm = this;
            this.ShowSearchData(search, vm, selctedValue);
        },
        setSelectedValue: async function (event, id, val) {
            debugger;
            var vm = this;
            vm.selectvalue = '';
            try {
                if (val.value != '') {
                    vm.selectvalue = val.value;
                }
                else {
                    vm.selectvalue = vm.FormSchema[0].Data[0].value;
                }
            }
            catch (ex) {

            }
            vm.setClientData(vm.selectvalue, null);
        },
        setClientData: async function (event, id) {
            var vm = this;
            var TicketId = null;
            if (id != null) {
                TicketId = id;
            } else {
                TicketId = event;
            }
            (vm.FormSchema)[0].Data[5].value = '';
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'CLIENTFROMTICKETID'}&search=&refId=` + TicketId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    if (response.data.status == "Success") {
                        (vm.FormSchema)[0].Data[5].value = response.data.result.DATA[0].value;
                        vm.GetClientaddress(null, response.data.result.DATA[0].value);
                    }
                    else {
                        vm.selectClientvalue = '';
                    }
                }
            });
        },
        ShowSearchData: async function (search, selctedValue) {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'FSM_Tickets_List'}&search=${search}&refId=`;
            if (typeof (selctedValue) == 'undefined') {
                selctedValue = 0;
            }
            vm.FormSchema[0].Data[0].config.options = [];
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        debugger;
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[0].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetDdlRecords_WorkType: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'FSM_WORKTYPE'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[3].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetProductRecords: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'PRODUCT'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[4].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetAddClientDDL: async function () {
            var vm = this;
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'CLIENT_LIST_WO'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[5].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetClientaddress: async function (event, id) {
            var vm = this;
            var clientId = 0;
            if (event == null) {
                clientId = id;
            } else {
                clientId = event.target.value.toString();
                vm.clientId = clientId;
            }
            vm.FormSchema[0].Data[6].config.options = [];
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'CLIENT_ADDRESS_LIST_WO'}&search=&refId=` + clientId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[6].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
            vm.GetUtilityDDL(event, id);
        },
        fetchAddressByClientId: async function (event, id) {
            var vueObj = this;
            vueObj.Latitude = '';
            vueObj.Longitude = '';
            var addressId = 0;
            if (event == null) {
                addressId = id;
            } else {
                addressId = event.target.value.toString();
            }
            var appendingUrl = `id=${vueObj.clientId}&&moduleName=${'Ticketing'}&&addressId=` + addressId;
            await DataServices.GetTicketClientAddress(appendingUrl).then(function (response) {
                if (response.data) {
                    var countryId = response.data.result.data[0].COUNTRY_ID;
                    vueObj.GetStateDDL(null, countryId);
                    (vueObj.FormSchema)[3].Data[0].value = response.data.result.data[0].USER_ADDRESS_1;
                    (vueObj.FormSchema)[3].Data[1].value = response.data.result.data[0].COUNTRY_ID;
                    (vueObj.FormSchema)[3].Data[2].value = response.data.result.data[0].CT[0].ST[0].STATE_ID;
                    (vueObj.FormSchema)[3].Data[3].value = response.data.result.data[0].USER_STATE;
                    (vueObj.FormSchema)[3].Data[4].value = response.data.result.data[0].USER_CITY;
                    (vueObj.FormSchema)[3].Data[5].value = response.data.result.data[0].USER_ADDRESS_2;
                    (vueObj.FormSchema)[3].Data[6].value = response.data.result.data[0].USER_ZIPCODE;
                    vueObj.Latitude = response.data.result.data[0].Latitude + ',' + response.data.result.data[0].Longitude;
                    vueObj.Longitude = response.data.result.data[0].Latitude + ',' + response.data.result.data[0].Longitude;
                }
            });
        },
        GetUtilityDDL: async function (event, id) {
            var clientId = 0;
            if (event == null) {
                clientId = id;
            } else {
                clientId = event.target.value.toString();
            }
            var vm = this;
            vm.FormSchema[0].Data[7].config.options = [];
            var appendingUrl = `refId=&&moduleName=${'Ticketing'}&type=${'UTILITY_LIST_BY_CLIENT_ID'}&search=&id=` + clientId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[7].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetCDCDdlData: async function (event) {
            var vm = this;
            var clientId = event.target.value.toString();
            vm.FormSchema[0].Data[8].config.options = [];
            var appendingUrl = `refId=&&moduleName=${'Ticketing'}&type=${'FSM_CDCCODE_FOR_TICKET'}&search=&id=` + clientId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[0].Data[8].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetCountryDDL: async function (event) {
            var vm = this;
            vm.FormSchema[3].Data[1].config.options = [];
            vm.CountryDDl = '';
            var appendingUrl = `id=&&moduleName=${'Ticketing'}&type=${'COUNTRIES'}&search=&refId=`;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    vm.CountryDDl = response.data.result.DATA;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[3].Data[1].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        GetStateDDL: async function (event, id) {
            var vm = this;
            var CountryId = 0;
            if (event == null) {
                CountryId = id;
            } else {
                CountryId = event.target.value.toString();
            }
            vm.FormSchema[3].Data[2].config.options = [];
            vm.StateDDl = '';
            var appendingUrl = `id=&&moduleName=${'FSM'}&type=${'STATE'}&search=&refId=` + CountryId;
            await DataServices.GetDDLDataFSM(appendingUrl).then(function (response) {
                if (response.data) {
                    var json = response.data;
                    vm.StateDDl = response.data.result.DATA;
                    if (json != null) {
                        response.data.result.DATA.forEach((item, index) => {
                            vm.FormSchema[3].Data[2].config.options.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            });
        },
        onSubmit(formfields, val) {
            debugger
            var vm = this;
            vm.buttons[0].class+=" disabled";
            var jsonobj = {
                WorkOrderId: vm.WorkOrdrId == null ? 0 : vm.WorkOrdrId,
                WorkOrderNumber: vm.WorkOrderNumber,
                WorkOrderDate: vm.$options.filters.formatDate(formfields.WorkOrderDate),
                WorkOrderTitle: formfields.WorkOrderTitle,
                WorkOrderDescription: formfields.Description,
                TicketId: formfields.TicketNumber.value != '' ? formfields.TicketNumber.value : vm.selectvalue,
                CDCMvalueId: formfields.CDC,
                workTypeId: formfields.WorkType,
                productId: formfields.Product,
                Address: formfields.Address,
                City: formfields.City,
                State: formfields.State,
                Zip: formfields.ZipCode == undefined ? '' : formfields.ZipCode,
                LOCATION_NW: vm.Latitude,
                LOCATION_SE: vm.Longitude,
                DIGSTREET: formfields.Street,
                County: formfields.County,
                CountryId: formfields.Country,
                StartDateTime: formfields.StartDate == "" ? "" : vm.$options.filters.formatDateTime(formfields.StartDate),
                EndDateTime: formfields.EndDate == "" ? "" : vm.$options.filters.formatDateTime(formfields.EndDate),
                ClientId: formfields.Client
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveWorkOrder(params).then((response) => {
                if (response.data.status == 'Success') {
                    debugger
                    if (response.data.result.MSG == 'EXIST') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("WorkOrder")]), "failure", true, vm.$t("Alert"));
                    }
                    else {
                        if (vm.WorkOrdrId > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("WorkOrder")]), "success", true, vm.$t("Alert"));                           
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("WorkOrder")]), "success", true, vm.$t("Alert"));
                        }                        
                    }
                    if(vm.form=='WorkOrder'){                        
                            vm.$parent.fetchRecords()
                            vm.Close();
                    }else{
                        vm.$parent.IsWorkOrderShow=true;
                        vm.$parent.isShowWorkList=false;
                        vm.$parent.isShowWorkOrder=false;
                        vm.$parent.LoadTab('workorder')
                        vm.Close();
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("WorkOrder")]), "failure", true, vm.$t("Alert"));
                    vm.Close();
                }
            });
        }
    }
}
</script>