                                    <RelatedTicketList v-if="isShowRelatedticket" :ticketId="TicketId"></RelatedTicketList>
                                </div>

                                <div class="tab-pane fade" id="tab-trackhistory-content">
                                    <TrackHistoryList v-if="isShowTrackHistory" :ticketId="TicketId"></TrackHistoryList>
                                </div>

                                <div class="tab-pane fade" id="tab-workorder-content">
