                                    <WorkOrderCardView v-if="!isShowWorkList && isShowWorkOrder" :isLocateorNonLocate="isLocateorNonLocate"  :ticketId="TicketId" v-bind:callbackfunction="FetchData"></WorkOrderCardView>
