import TrackHistoryList from "./DetailsComponent/TrackHistoryList.vue"
import PartialTicketConclusion from './DetailsComponent/PartialTicketConclusion.vue';
import WorkOrderList from "./DetailsComponent/WorkOrderList.vue"
import WorkOrderCardView from './DetailsComponent/WorkOrderCardView.vue'
import FollowUp from '@/components/Modal/AddFollowUpList.vue';
import AddClaimData from "@/components/Modal/AddClaimModal.vue";
import MergeTicketData from "@/components/Modal/MergedTicketModal.vue";
