import ShowAttachmentData from "../../views/Ticketing/ShowAttachmentData.vue";
import ticketupdate from "./Update.vue";
import transfertickets from "./AssignTransferTicket.vue";
import MapComponent from "../../components/Common/MapComponent.vue";
import modalReplyTicketpopup from '../../components/Modal/modalReplyTicket.vue';
import WorkOrderTimerPopup from "@/components/Modal/WorkOrderTimerModal.vue";
import VueEasyLightbox from 'vue-image-lightbox-carousel';
export default {
    components: {
        ticketdetails,
        ThreadsList,
        TicketNotes,
        refreshKey:0,
        TicketAttachment,
        AddEscalatePopUp,
        TicketClaim,
        TicketCalls,
        FollowUp,
        ShowAttachmentData,
        AddNoteData,
        AddClaimData,
        MergeTicketData,
        ticketupdate,
        transfertickets,
        MapComponent,
        modalReplyTicketpopup,
        WorkOrderTimerPopup,
        RelatedTicketList,
        TrackHistoryList,
        WorkOrderList,
        VueEasyLightbox,
        AddWorkOrderModal,
        WorkOrderCardView,
        PartialTicketConclusion
    },
    data() {
        return {
            config: {
                isShowDrawButton: false,
                isShowEtaMap: true,
                isShowLocator: false,
                isShowEtaPipeLines: false,
                isShowGetDirection: true
            },
            isLoading: false,
            TicketId: null,
            TicketNumber: null,
            AddWorkOrderModal: false,
            TicketData: [],
            TicketDetailData: [],
            UserType: null,
            user_id: 0,
            AssignToId:0,
            isTicketResolve: null,
            galleryImages: [],
            ConfigData: [],
            ShowWorkOrderTimer: "no",
            isTicketHOD: null,
            TicketThreadsData: [],
            ConclusionTicket: [],
            AttachmentData: [],
            NoteData: [],
            visible: false,
            ClaimData: [],
            CallData: [],
            userstatus: '',
            isShowTicketDetail: false,
            isShowActivities: false,
            isShowAddClaim: false,
            isShowEscalate: false,
            isShowNotes: false,
            isShowClaims: false,
            isShowCalls: false,
            isShowRelatedticket: false,
            isshowFolloupList: false,
            isShowAddNote: false,
            isShowMergeTicket: false,
            isShowTicketUpdate: false,
            isShowTransferTicket: false,
            showAttachmentModal: false,
            showReplyTicketModal: false,
            isShowTrackHistory: false,
            isShowWorkOrder: false,
            isShowWOTimer: false,
            UserStatus: null,
            mapObj: [],
            UtilityMapData: [],
            ticketType: null,
            isShowMap: true,
            mapHeaderText: 'Collapse Map',
            TicketDetailDataForPopup: [],
            PageNumber: 1,
            PageSize: 10,
            TotalRecords: 0,
            ParentCompanyId: null,
            ParentCompanyName: null,
            ShowTimer: false,
            AddEditText: '',
            SortBy: '',
            SortExp: '',
            noRecord: false,
            index: 0,
            isLocateorNonLocate: '',
            ListOfCCUsers: '',
            ticketStatus: '',
            isDisabledHeaderButton: false,
            isShowTicketConclusion:false,
            mapType: null,
            SourcePage: null,
            Showtab: 1,
            isShowWorkList:false,
            IsWorkOrderShow:false,
            Status_id: null,
            userName: null
        }
    },
    mounted() {
        var vm = this;
        const sourcePage = this.$route.query.source;
        vm.SourcePage = sourcePage;
    },
    created() {
        debugger
        var vm = this;
        vm.str_ticket_id = vm.$route.params.id;
        vm.UserType = vm.GetUserInfo.UserType;
        vm.userName = vm.GetUserInfo.Name;
        vm.user_id = this.GetUserInfo.ID
        vm.Status_id = vm.$route.query.status_id;
        vm.FetchData();
        vm.UserCurrentStatus();

    },
    updated: function () {
        var vm = this;
        setTimeout(function () {
            vm.setHeaderForSelectedTab();
        }, 500);
    },
    methods: {
        handleHide: function () {
            this.visible = false
        },
        changeWorkOrderView:function(ths){
            if(ths=='Card'){
                this.isShowWorkList=false;              
            }else{
                this.isShowWorkList=true;
            }
        },
        ancreplyExpandAllCollapseAll: async function () {
            var vm = this;
            if (vm.Showtab == 0) {
                vm.Showtab = 1;
                $("#tab-ticket-content").find("#accordion").find('.collapse').addClass('show');
            } else {
                vm.Showtab = 0;
                $("#tab-ticket-content").find("#accordion").find('.collapse').removeClass('show');
            }
        },
        openSpecificImage(attachmentPath, attachmentFileName) {
            var vm = this;
            var obj = {
                path: attachmentPath,
                caption: attachmentFileName.split('.')[0]
            }
            vm.galleryImages.push(obj);
        },
        showImg: function (index) {
            this.index = index
            this.visible = true
            $('#div_ShowGpxFile').html("");
        },
        openStartPopUp: function () {
            var vm = this;
            vm.isShowWOTimer = true;
        },
        openEscalatePopUp: function () {
            var vm = this;
            vm.isShowEscalate = true;
            vm.ParentCompanyId = vm.ParentCompanyId;
            vm.ParentCompanyName = vm.ParentCompanyName;
        },
        
        urlChange: function () {
            const query = Object.assign({}, this.$route.query); query.reload = 'reload'
            this.$router.push({ query: query })
        },
        openStopPopUp: function () {
            var vm = this;
            var jsonobj =
            {
                TicketId: vm.TicketId,
                TrackId: 0,
                RequestFor: 'FSM',
                RequestForType: 'STOP',
                IPAddress: ''

            };
            var params = JSON.stringify(jsonobj);
            vm.confirmR(vm.$t('AreYouSureToStopTimer'), true, vm.$t('StopTimer'), function (result) {
                vm.Disabled = 1;
                DataServices.StopTrackHistory(params).then((response) => {
                    try {
                        if (response.data.status == 'Success') {
                            vm.$parent.ShowTimer = false;
                            vm.AddEditText = "Add";
                            vm.Disabled = 0;
                            vm.urlChange();
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("TimeTrack")]), "success", true, vm.$t("Alert"));
                            // vm.FetchData();
                            document.getElementById('tab-trackhistory').click();
                        }
                        else {
                            vm.ShowAlert(vm.$t("AddedError", [vm.$t("TimeTrack")]), "failure", true, vm.$t("Alert"));
                            vm.Disabled = 0;
                            vm.Close();
                        }
                    }
                    catch (exinner) {
                        return HandleErrorOccured(exinner, 'failure');
                    }
                });
            });
        },
        openReplyModel: function () {
            var vueObj = this;
            vueObj.showReplyTicketModal = true;
        },

        FetchData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `str_ticket_id=${vm.str_ticket_id}&moduleName=Ticketing&subModuleCode=Ticketing`;
            await DataServices.GetTicketDetails(url).then(function (response) {
                vm.isLoading = false;
                //debugger;
                let ticketData = {};
                vm.ticketType = response.data.result.IS_FIELD_SERVICE;
                vm.ConfigData = response.data.result.CONFIG_DATA;
                if (response.data.result.LEFTDATA.length > 0) {
                    var result = response.data.result.LEFTDATA;
                    vm.TicketDetailDataForPopup = response.data.result.LEFTDATA;
                    vm.UtilityMapData = response.data.result.FSM_MAP_CONFIG;
                    vm.mapType = response.data.result.FSM_MAP_CONFIG[0].TYPE;
                    result.forEach(function (row) {
                        if (row.name == "ticket_id") {
                            ticketData["ticketId"] = row.value;

                        }
                        else if (row.name == "sla_id") {
                            ticketData["TicketSlaId"] = row.value;
                        }
                        else if (row.name == "ticket_number") {
                            ticketData["ticket_number"] = row.value;
                        }
                        else if (row.name == "created_by") {
                            ticketData["createdBy"] = row.value;
                        }

                        else if (row.name == "assigned_to") {
                            ticketData["assignedTo"] = row.value;
                            vm.AssignToId=row.value;
                        }
                        else if (row.name == "external_ticket_number") {
                            ticketData["external_ticket_number"] = row.value;
                        }
                        else if (row.name == "ticket_subject") {
                            ticketData["ticketSubject"] = row.value;
                        }
                        else if (row.name == "status_id") {
                            ticketData["status"] = row.value;
                        }
                        else if (row.name == "ticket_category_id") {
                            ticketData["ticketCategoryName"] = row.value;
                        }
                        else if (row.name == "priority_id") {
                            ticketData["priority"] = row.value;
                        }
                        else if (row.name == "created_at") {
                            ticketData["createdAt"] = row.value;
                        }
                        else if (row.name == "expected_resolution_date") {
                            ticketData["expectedResolutionDate"] = row.value;
                        }
                        else if (row.name == "ticket_department_id") {
                            ticketData["ticketDepartmentId"] = row.value;
                        }
                        // else if (row.name == "Description") {
                        //    ticketData ["description"] = row.value;
                        // }
                        else if (row.name == "location_nw") {
                            ticketData["location_nw"] = row.value;
                        }
                        else if (row.name == "location_se") {
                            ticketData["location_se"] = row.value;
                        }
                        else if (row.name == "created_by") {
                            ticketData["createdBy"] = row.value;
                        }
                        else if (row.name == "is_support_ticket") {
                            ticketData["isSupportTicket"] = row.value;
                        }
                        else if (row.name == "chain_id") {
                            ticketData["chainId"] = row.value;
                        }
                    });
                    ticketData["noOfNotes"] = response.data.result.NO_OF_NOTES;
                    ticketData["attachmentsCount"] = response.data.result.ATTACHMENTS_COUNT;
                    ticketData["noOfClaims"] = response.data.result.NO_OF_CLAIM;
                    ticketData["isFieldServiceTicket"] = response.data.result.IS_FIELD_SERVICE == 1 ? true : false;
                    ticketData["departmentName"] = response.data.result.DEPARTMENT_NAME;
                    ticketData["followUpId"] = response.data.result.FOLLOW_UP_ID;
                    ticketData["raisedBy"] = response.data.result.RAISED_BY;
                    ticketData["raisedByDepartmentName"] = response.data.result.DEPT_NAME;
                    ticketData["createdUserTypeId"] = response.data.result.TICKET_CREATED_USER_TYPE;
                    ticketData ["description"] = response.data.result.TICKET_DESCRIPTION;
                    vm.isLocateorNonLocate = response.data.result.IS_FSM_LOCATING;
                    vm.ListOfCCUsers = response.data.result.CC_USERS_NON_LOCATE;
                    //    vm.isLocateorNonLocate = true;
                    vm.TicketData = ticketData;
                    vm.TicketNumber = vm.TicketData.ticket_number;
                    vm.TicketId = vm.TicketData.ticketId;
                    console.log("TicketId", vm.TicketId);
                    vm.ParentCompanyId = response.data.result.PARENT_COMPANY_ID;
                    vm.ParentCompanyName = response.data.result.PARENT_COMPANY_NAME;
                    if (vm.TicketData.status == 'Closed' || vm.TicketData.status == 'Closed and Locked' || vm.TicketData.status == 'Cancelled' || vm.TicketData.status == 'Completed' || vm.TicketData.status == 'Draft') {
                        vm.isDisabledHeaderButton = true;
                    }
                    vm.isTicketResolve = response.data.result.IS_TICKET_RESOLVE;
                    if (response.data.result.SHOW_Work_Order_Timer.CONFIGURATION_Work_Order_Timer == 'yes') {
                        vm.ShowWorkOrderTimer = 'yes'
                    }
                    else {
                        vm.ShowWorkOrderTimer = 'no'
                    }

                    if (response.data.result.IN_COMPLETE_ENTRY == '1') {
                        vm.AddEditText = "Edit";
                    }
                    vm.isTicketHOD = response.data.result.IS_TICKET_HOD;
                    vm.UserStatus = response.data.result.STATUS_CODE.toLowerCase();
                    setTimeout(function () {
                        vm.LoadTab('activities');
                    }, 500);
                    var obj = {
                        latlong: vm.TicketData.location_nw,
                        refValue: vm.TicketNumber,
                        popupHeaderHtml: '',
                        popupHeaderColor: "",
                        popupBodyHtml: '',
                        popupFooterHtml: '',
                        pinLatLong: {
                            latitude: vm.TicketData.location_nw,
                            longitude: vm.TicketData.location_nw
                        },
                        polygons: [
                            {
                                name: "",
                                latLong: [{
                                    latitude: vm.TicketData.location_nw,
                                    longitude: vm.TicketData.location_nw,
                                }
                                ]
                            }
                        ],
                        address: "",
                        priority: '',
                        color_code: ''
                    }

                    vm.mapObj.push(obj);
                }

                if (response.data.result.CONFIG_DATA.length > 0) {
                    var configResult = response.data.result.CONFIG_DATA;
                    configResult.forEach(function (row) {
                        if (row.CORE_CONFIG_KEY == "TicketDisallowedFileTypes") {
                            ticketData["disallowedFileTypes"] = row.CONFIG_DATA_VALUE;

                        }

                    });
                }


            })
        },
        ShowHideMap: function () {
            var icon = $('#icnToggleMap');
            if (this.isShowMap == false) {
                this.isShowMap = true;
                this.mapHeaderText = 'Collapse map'
                if (icon.hasClass('fa-angle-double-down')) {
                    icon.removeClass('fa-angle-double-down');
                    icon.addClass('fa-angle-double-up');
                }  
            }
            else {
                this.isShowMap = false;
                this.mapHeaderText = 'Expand map'
                if (icon.hasClass('fa-angle-double-up')) {
                    icon.removeClass('fa-angle-double-up');
                    icon.addClass('fa-angle-double-down');
                }
            }
        },
        FetchActivityData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `ticketId=${vm.str_ticket_id}`;
            await DataServices.GetThreadsList(url).then(function (response) {
                vm.isLoading = false;
                var result = response.data.TicketThreads;
                vm.TicketId = response.data.ticket_id;

                var Geturl = `ticketId=${vm.TicketId}`;
                DataServices.GetTicketConclusionStatus(Geturl).then(function (response) {
                    vm.ConclusionTicket = response.data.result;
                    if (vm.ConclusionTicket.length > 0) {
                        var ISShowSaveConclusion = false;
                        for (let index = 0; index < vm.ConclusionTicket.length; index++) {
                            const item = vm.ConclusionTicket[index];
                            if (item.STATUS_CODE == 'ACT') {
                                ISShowSaveConclusion = true;
                                break;
                            }
                        }
                    }
                    if ((vm.TicketData.status == 'Closed' || vm.TicketData.status == 'Closed and Locked') &&(vm.isTicketHOD=='yes' || vm.UserType=='CA') && (vm.ConclusionTicket.length == 0 || ISShowSaveConclusion==false)) {
                        vm.isShowTicketConclusion = true;
                    }
                })

                if (result.length > 0) {
                    vm.TicketThreadsData = result;
                }
            })
        },
        FetchTicketData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `id=${vm.TicketId}&moduleName=Ticketing&subModuleCode=Ticketing`;
            await DataServices.GetTicketDetails(url).then(function (response) {
                vm.isLoading = false;
                var result = response.data.result.LEFTDATA;
                if (result.length > 0) {
                    vm.TicketDetailData = response.data.result.LEFTDATA;
                }
            })
        },
        GetNoteData: async function () {
            //debugger
            var vm = this;
            vm.isLoading = true;
            var url = `ticketId=${vm.TicketId}`;
            await DataServices.GetWorkOrderNoteList(url).then(function (response) {
                vm.isLoading = false;
                if (response.data.length > 0) {
                    vm.NoteData = response.data;
                    //  vm.ticketData.noOfNotes = response.data.length;
                    vm.TicketData.noOfNotes = response.data.length;
                    vm.GetAttachmentData();
                }
                else {
                    vm.NoteData = [];
                }
            })
        },
        GetClaimData: async function () {
            var vm = this;
            debugger;
            vm.isLoading = true;
            var url = `ticketId=${vm.TicketId}&claimId=0&isHod=`;
            await DataServices.TicketClaimList(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null) {
                    ////debugger;
                    vm.ClaimData = response.data;
                    vm.TicketData.noOfClaims = response.data.claim.length;
                    vm.refreshKey+=1;
                }
                else {
                    vm.ClaimData = [];
                }
            });

        },
        GetCallData: async function () {
            var vm = this;
            vm.isLoading = true;
            var url = `ticketId=${vm.TicketId}&subModuleCode=Ticketing&reqFrom=`;
            await DataServices.GetCallList(url).then(function (response) {
                vm.isLoading = false;
                if (response.data != null) {
                    vm.CallData = response.data;
                }
                else {
                    vm.CallData = [];
                }
            });

        },
        openAttachmentModel: function () {
            var vueObj = this;
            vueObj.showAttachmentModal = true;
            $("#divAttachment").scrollTop(0);
        },

        GetAttachmentData: function () {
            //debugger;
            var vueObj = this;
            vueObj.galleryImages = [];
            vueObj.AttachmentData = [];
            vueObj.galleryFilesView = [];
            vueObj.isLoading = true;
            var url = `ticketId=${vueObj.TicketId}`;
            DataServices.GetTicketFiles(url).then(function (response) {
                vueObj.isLoading = false;
                if (response.data.result != "" && response.data.result.DATA.length > 0) {
                    vueObj.AttachmentData = response.data.result.DATA;
                    vueObj.TicketData.attachmentsCount = response.data.result.DATA.length;
                }
                else {
                    vueObj.TicketData.attachmentsCount = [];
                    vueObj.AttachmentData = [];
                }
            });
        },
        async LoadTab(tab) {
            var vm = this;
            debugger;
            if (tab == 'activities') {
                vm.isShowCalls = false;
                vm.isShowClaims = false;
                vm.isShowWorkOrder = false;
                vm.isShowRelatedticket = false;
                vm.isShowTrackHistory = false;
                vm.isShowNotes = false;
                vm.isShowActivities = true;
                vm.isShowTicketDetail = true;
                vm.FetchActivityData();
            }
            else if (tab == 'notes') {
                await vm.GetNoteData();
                vm.isShowActivities = false;
                vm.isShowTicketDetail = false;
                vm.isShowCalls = false;
                vm.isShowClaims = false;
                vm.isShowWorkOrder = false;
                vm.isShowRelatedticket = false;
                vm.isShowTrackHistory = false;
                vm.isShowNotes = true;
            }
            else if (tab == 'attachments') {
                await vm.GetAttachmentData();
                vm.isShowActivities = false;
                vm.isShowTicketDetail = false;
                vm.isShowNotes = false;
                vm.isShowCalls = false;
                vm.isShowClaims = false;
                vm.isShowWorkOrder = false;
                vm.isShowRelatedticket = false;
                vm.isShowTrackHistory = false;
            }
            else if (tab == 'claims') {
                await vm.GetClaimData();
                vm.isShowActivities = false;
                vm.isShowTicketDetail = false;
                vm.isShowNotes = false;
                vm.isShowCalls = false;
                vm.isShowWorkOrder = false;
                vm.isShowRelatedticket = false;
                vm.isShowTrackHistory = false;
                vm.isShowClaims = true;
            }
            else if (tab == 'calls') {
                await vm.GetCallData();
                vm.isShowActivities = false;
                vm.isShowTicketDetail = false;
                vm.isShowNotes = false;
                vm.isShowClaims = false;
                vm.isShowWorkOrder = false;
                vm.isShowRelatedticket = false;
                vm.isShowTrackHistory = false;
                vm.isShowCalls = true;
            }
            else if (tab == 'relatedticket') {
                vm.isShowActivities = false;
                vm.isShowTicketDetail = false;
                vm.isShowNotes = false;
                vm.isShowCalls = false;
                vm.isShowClaims = false;
                vm.isShowTrackHistory = false;
                vm.isShowWorkOrder = false;
                vm.isShowRelatedticket = true;

            }
            else if (tab == 'trackhistory') {
                vm.isShowActivities = false;
                vm.isShowTicketDetail = false;
                vm.isShowNotes = false;
                vm.isShowCalls = false;
                vm.isShowClaims = false;
                vm.isShowWorkOrder = false;
                vm.isShowRelatedticket = false;
                vm.isShowTrackHistory = true;
            }
            else if (tab == 'workorder') {
                vm.isShowActivities = false;
                vm.isShowTicketDetail = false;
                vm.isShowNotes = false;
                vm.isShowCalls = false;
                vm.isShowClaims = false;
                vm.isShowRelatedticket = false;
                vm.isShowTrackHistory = false;
                vm.isShowWorkOrder = true;
                vm.isShowWorkList=true;
                if(vm.IsWorkOrderShow ==true){
                    vm.isShowWorkList=false;
                    vm.isShowWorkOrder = false;
                    setTimeout(function () {
                        vm.isShowWorkList=true;
                        vm.isShowWorkOrder = true;
                                    }, 1000);
                    

                }
            }
        },

        UserCurrentStatus: function () {
            var vm = this;
            if (vm.userstatus == "online") {
                vm.confirmR(vm.$t("TicketUserStatusChangeMessage"), true, vm.$t("Alert"), function () {
                    var url = `statusCode=${vm.userstatus}`;
                    DataServices.UpdateUserCurrentStatus(url).then(function (response) {

                        if (response.data == 'true') {
                            vm.userstatus = response.status_code;
                        }
                        else {
                            vm.userstatus = response.status_code;
                        }
                    })
                });
            }
            else if (vm.userstatus != "online" && vm.userstatus != "") {
                vm.confirmR(vm.$t("TicketUserStatusChangeMessage"), true, vm.$t("Alert"), function () {
                    var url = `statusCode=${vm.userstatus}`
                    DataServices.UpdateUserCurrentStatus(url).then(function (response) {
                        if (response.data.length > 0) {
                            vm.userstatus = response.data[0].status_code;
                        }
                        else {
                            vm.userstatus = response.data[0].status_code;
                        }
                    })
                });
            }
            else if (vm.userstatus == "") {
                var url = `statusCode=${vm.userstatus}`
                DataServices.UpdateUserCurrentStatus(url).then(function (response) {

                    if (response.data.length > 0) {
                        vm.userstatus = response.data[0].status_code;
                    }
                    else {
                        vm.userstatus = response.data[0].status_code;
                    }
                })
            }
        },
        UpdateTicket: async function () {
            var vm = this;
            vm.isShowTicketUpdate = true;
        },
        AddWorkOrder: async function () {
            var vm = this;
            vm.AddWorkOrderModal = true;
        }
    }
}
</script>