<template>
    <div class=""  v-if="IsShowConclusionTab">
        <loader :is-visible="isLoading"></loader>
        <div class="row barrw" id="divconclusionheader">
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="heading-border">
                            <h5 class="h5  mb-3">
                                <span>{{ $t('Conclusion') }} </span>
                            </h5>
                        </div>
                    </div>
                </div>
        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
        <template>
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                <div class="row barrw" id="divconclusionheader">
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="heading-border">
                            <h5 class="h5  mb-3">
                                <span> {{ $t('ConclusionThread') }}</span>
                            </h5>
                        </div>
                    </div>
                </div>
                <div class="conclusion-ticket" style="max-height:248px; overflow:auto;">
                    <div class="conclusion-ticket-content" id="divconclusiondata">
                        <div id="tblconclusionlist"></div>
                        <div class="d-block text-center text-danger border p-2 no-record" v-if="ConclusionData == [] || ConclusionData == ''">
                            {{ $t('NoRecordfound') }}  </div>
                        <div id="trconclusionList_@list.ConclusionId" v-for="(item, itemIndex) in ConclusionData">

                            <span class="header-action float-right d-none"
                                v-if="item.CREATED_BY == user_id && item.STATUS_CODE == 'TICKET_CONCLUSION_DRAFT'">
                                <a title="Edit" class="editTicketConclusion  btn-success"
                                    :data-ConclusionId="item.CONCLUSION_ID" :data-RootCause="item.CORRECTIVE_ACTION"
                                    :data-CorrectiveAction="item.CORRECTIVE_ACTION"><i class="fa fa-pencil"></i></a>
                            </span>
                            <strong class="font-weight-bold" v-if="item.ROOT_CAUSE != null">{{ $t('RootCause') }}:</strong>
                            {{ item.ROOT_CAUSE }}
                            <div class="clearfix mt-2"></div>
                            <strong class="font-weight-bold" v-if="item.CORRECTIVE_ACTION != null">{{ $t('CorrectiveAction') }} :</strong>
                            {{ item.CORRECTIVE_ACTION }}
                            <div class="text-right d-block mt-2">
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left p-0 text-left"><i
                                        class="font-weight-bold">{{ $t('PostedBy') }}:</i> {{ item.CREATEDBYNAME }}</div>
                                <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left p-0 text-right"><i
                                        class="font-weight-bold">{{ $t('CreatedAt') }}:</i> {{
                                            $options.filters.formatDate(item.CREATED_AT) }} </div>
                            </div><br><br>
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </div>
</template>
<script>
import DataServices from "@/services/DataService";
export default {
    props: {
        TicketId: {
            type: Number,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            ConclusionId: 0,
            user_id: 0,
            IsShowConclusionTab: true,
            ConclusionData: [],
            StatusCode: 'N',
            FormSchema: [
                {
                    Data: [
                        {
                            astype: "TextAreaField",
                            label: this.$t('RootCause'),
                            name: "RootCause",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        },
                        {
                            astype: "TextAreaField",
                            label: this.$t('CorrectiveAction'),
                            name: "CorrectiveAction",
                            value: "",
                            validationRules: { "required": true, max: 100 },
                        }
                    ],
                }
            ],
            buttons: [
                {
                    type: "SaveAsDraft",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-save pr-2'></i>Save As Draft",
                },
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                }
            ],
        }
    },
    created: async function () {
        var vm = this;
        vm.user_id = this.GetUserInfo.ID
        if (vm.TicketId > 0) {
            this.LoadConclusionTab();
        }
    },
    methods: {
        LoadConclusionTab: async function () {
            var vm = this;
            var url = `ticketId=${vm.TicketId}`;
            await DataServices.LoadConclusionTab(url).then(function (response) {
                if (response.data.status == 'Success') {
                    vm.ConclusionData = response.data.result;
                }
            })
        },
        onSubmit(formfields, val) {
            debugger
            var vm = this;
            var htmlString = val;
            var match = htmlString.match(/<i[^>]*>[^<]*<\/i>(.*)/);
            var extractedText = match ? match[1] : null;
            if (extractedText == 'Save') {
                vm.StatusCode = 'N';
            } else {
                vm.StatusCode = 'Y';
            }

            var jsonobj = {
                ConclusionId: vm.ConclusionId == null ? 0 : vm.ConclusionId,
                TicketId: vm.TicketId,
                RootCause: formfields.RootCause,
                CorrectiveAction: formfields.CorrectiveAction,
                StatusCode: vm.StatusCode
            };
            var params = JSON.stringify(jsonobj);
            DataServices.SaveTicketConclusion(params).then((response) => {
                if (response.data.status == 'Success') {
                    var rslt = JSON.parse(response.data.result);
                    if (rslt.MSG == 'ALREADY EXISTS') {
                        vm.ShowAlert(vm.$t("AlreadyExists", [vm.$t("Conclusion ")]), "failure", true, vm.$t("Alert"));
                    }
                    else {
                        if (vm.Conclusion > 0) {
                            vm.ShowAlert(vm.$t("UpdatedSuccess", [vm.$t("Conclusion ")]), "success", true, vm.$t("Alert"));
                        }
                        else {
                            if(vm.StatusCode=='N'){
                            vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Conclusion ")]), "success", true, vm.$t("Alert"));
                        }else{
                            vm.ShowAlert(vm.$t("DraftedSuccess", [vm.$t("Conclusion ")]), "success", true, vm.$t("Alert"));
                        }
                        }
                        vm.IsShowConclusionTab=false;
                        vm.LoadConclusionTab();
                        vm.$parent.FetchData();
                        var divToRemove = document.querySelector('.border.p-3.mb-3');
                        divToRemove.parentNode.removeChild(divToRemove);
                    }
                }
                else {
                    vm.ShowAlert(vm.$t("AddedError", [vm.$t("Conclusion ")]), "failure", true, vm.$t("Alert"));
                }
            });
        }
    }
}
</script>