<template>
    <section>
        <div class="col-lg-12 mb-2 mt-2 p-0" id="week-stat">
            <div class="expand-list">
                <a href="javascript:;" class="expand-list-btn justify-content-center align-items-center">
                    <i class="icon-solid arrow-solid-left" style="font-size:9pt"></i>
                </a>
            </div>
            <div class="border eqcol h-100 pt-0 px-3 pb-3">
                <div class="updatefull-widg">
                    <div class="row border-bottom bg-f5f5f5 mb-2">
                        <div class="col-md-6">
                            <h6 class="py-3 mb-0">
                                {{ $t('Raised') + ' ' + $t('By') }}:
                                <template v-if="TicketData.createdBy == GetUserInfo.ID && UserType == 'CL'">
                                    <span class="blue-time">{{ TicketData.raisedBy }}</span>{{ $t('You') }}
                                </template>
                                <template v-else>
                                    <span class="blue-time">{{ TicketData.raisedBy }}</span>
                                    <template v-if="TicketData.createdUserTypeId == 4">
                                        {{ $t('Client') }}
                                    </template>
                                    <template v-else>
                                        ({{ TicketData.departmentName.DEPT_NAME }})
                                    </template>
                                </template>
                            </h6>
                        </div>
                        <div class="col-md-6 text-right">
                            <h6 class="py-3 mb-0">{{ $t('AssignedTo') }}:
                                <span class="blue-time">{{ TicketData.assignedTo == null ? $t('NotAssigned') : TicketData.assignedTo }} </span>
                                    ({{isBlank(TicketData.ticketDepartmentId) == true ? '' : TicketData.ticketDepartmentId}})
                            </h6>
                        </div>
                    </div>
                    <div class="row pt-1 px-3">
                        <div class="col-lg-6 p-0">
                            <h2 class="time_info">
                                {{ TicketData.ticketSubject }}
                                <span :class="getStatusClass(TicketData.status, 'status_name', 'fsm')" style="font-size: 16px;display: inline !important;">
                                    {{ TicketData.status }}
                                </span>
                            </h2>
                            <p class="float-left col-sm-12 col-lg-6 p-0"><b>{{ $t('Category') }}:</b>
                                {{ $t(TicketData.ticketCategoryName) == '' ? '--' : $t(TicketData.ticketCategoryName) }}
                            </p>
                            <p class="float-left col-sm-12 col-lg-6 p-0">
                                <b>{{ $t('Priority') }}: </b>
                                <span v-if="TicketData.priority == 'Critical'"
                                    class="Timer d-inline text-white critical-color" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else-if="TicketData.priority == 'Medium'"
                                    class="Timer d-inline text-white noti-blue" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else-if="TicketData.priority == 'High' || TicketData.priority == 'Highest'"
                                    class="Timer d-inline text-white noti-orange" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else-if="TicketData.priority == 'Low'" class="Timer d-inline text-white noti-green"
                                    style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                                <span v-else class="Timer d-inline text-white noti-blue" style="cursor:default;">
                                    <span class="tag-name px-2 text-center">
                                        {{ TicketData.priority }}
                                    </span>
                                </span>
                            </p>
                            <p class="mb-1 float-left col-sm-12 col-lg-6 p-0"><b>{{ $t('OpenDate') }}:
                                </b>{{ $options.filters.formatDateTime(TicketData.createdAt) }}</p>
                            <p class="float-left col-sm-12 col-lg-6 p-0"><b>{{ $t('Expected_Resolution') }}: </b>
                                {{ TicketData.expectedResolutionDate == null ? '--' :
                                    $options.filters.formatDate(TicketData.expectedResolutionDate) }}
                            </p>
                        </div>
                    </div>
                    <div class="mb-1 col-12 p-0">
                        <b>{{ $t('CCuppercase') }}: {{ ListOfCCUsers }}</b>
                    </div>
                    <div class="ticketShortDesciption mb-2 mt-2">
                        <div class="addednote-ticket-content">
                            <div class="col-12 p-0" id="divlessdata">
                                <div class="readmore textelipsis_inner">
                                    <div>
                                        <b> {{ $t('Description') }}: </b>
                                        <span v-html="TruncateStr(TicketData.description, 185)"></span>
                                    </div>
                                </div>
                            </div>
                            <div class="col-12 p-0" id="divshowmoredata" style="display:none;pointer-events:none;">
                                <div class="readmore">
                                    <div>
                                        <b> {{ $t('Description') }}: </b>
                                        <span v-html="TicketData.description"></span>
                                    </div>
                                </div>
                            </div>

                            <!-- && TicketData.description.length > 185 -->
                            <a v-if="TicketData.isFieldServiceTicket == false && isBlank(TicketData.external_ticket_number)"
                                href="javascript:;" v-on:click="showTicketDetail" class="lessmoredesc ancreadmoredesc">{{
                                    $t('readmore') }}</a>
                            <a v-if="TicketData.isFieldServiceTicket == true && !isBlank(TicketData.external_ticket_number)"
                                href="javascript:;" v-on:click="openTicketTextTab" class="lessmoredesc ancreadmoredesc">{{
                                    $t('readmore') }}</a>
                            <a v-else-if="TicketData.isFieldServiceTicket == true && isBlank(TicketData.external_ticket_number)"
                                href="javascript:;" v-on:click="showTicketDetail" class="lessmoredesc ancreadmoredesc">{{
                                    $t('readmore') }}</a>
                            <div class="addednote-ticket-content readmoredesc" style="display:none;">
                                <a href="javascript:;" class="anclessmoredesc"> {{ $t('Read') + ' ' + $t('Less') }} </a>
                            </div>
                        </div>
                    </div>
                </div>
                <!-- //Usic Project communication Section hide As per Discusion with hardeep Sir Dated 29-junue 2023
            @if (TALYGEN.StatusClass.CheckExtraFeatures("ACCOUNTSETUP", loggedUserInfo.CompanyId))
            {

            }
            else
            {
                if (Model.OnBehalfConsumerId == null && !Model.IsSupportTicket)
                { -->
                <div class="communication"></div>
                <div class="row">
                    <div class="col-md-12 d-flex">
                        <p class="mr-4">{{ $t('Call') }}: <a href="javascript:;" id="call" title="Call" class="voicecall">
                                <i class="fa fa-phone call-color"></i></a></p>
                        <p class="mr-4">{{ $t('VideoCall') }}: <a href="javascript:;" id="videocall" title="Video Call"
                                class="videocall"> <i class="fa fa-video-camera video-color"></i></a></p>
                        <p class="mr-4">{{ $t('SMS') }}: <a href="javascript:;" id="sms" title="Send SMS" class="sendsms"><i
                                    class="fa fa-commenting sms-color"></i></a></p>
                        <p class="mr-4">{{ $t('Chat') }}: <a href="javascript:;" id="chat" title="Chat" class="chat"><i
                                    class="fa fa-comments chat-color"></i></a></p>
                    </div>
                </div>
                <!-- }
            } -->
            </div>
        </div>

        <transition name="modal">
            <div class="my-popups">
                <loader :is-visible="isLoading"></loader>
                <div class="modal d-block dialog" v-if="isMoreTicketDetail">
                    <div class="modal-dialog">
                        <div class="modal-content">
                            <div class="modal-header theme-primary partition-full">
                                <h4 class="modal-title" v-if="isTicketDetail">{{ $t('TicketDetails') }}</h4>
                                <h4 class="modal-title" v-if="isTicketText">{{ $t('TicketText') }}</h4>
                                <button type="button" class="close" v-on:click="Close">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>
                            </div>

                            <div class="modal-body">
                                <div class="cardv-body float-left pt-0 border col-12 float-left p-1">
                                    <div class="card-text evemgmt_details">
                                        <div v-if="isTicketDetail"
                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left panel1 mb-0 p-0"
                                            style="min-height:100px;max-height: 500px; overflow-x: hidden;">
                                            <template v-for="item in LeftViewData">
                                                <p class="px-2 w-50 float-left"
                                                    v-if="(item.display_name == 'Ticket Number' || item.display_name == 'Ticket Subject' || item.display_name == 'Priority' || item.display_name == 'Created At'
                                                        || item.display_name == 'Modified At' || item.display_name == 'Zip' || item.display_name == 'Type of Work' || item.display_name == 'State' || item.display_name == 'City' || item.display_name == 'Country' || item.display_name == 'Address' || item.display_name == 'Description' || item.display_name == 'Ticket Number' || item.display_name == 'Last Activity Date' || item.display_name == 'Status' || item.display_name == 'Company Name')">
                                                    <i class="fa fa-dot-circle-o text-dark float-left pr-2"
                                                        style="font-size:12pt;"></i>
                                                    <span class="pl-1 font-weight-bold">{{ GetLocalizedValue(item.name,
                                                        item.display_name) }}: </span>
                                                    <span v-html="item.value"
                                                        v-if="item.name != 'status_id' && item.value != null"></span>
                                                    <span v-else-if="item.value == null">--</span>
                                                    <span v-if="item.name == 'status_id'"
                                                        :class="{ 'bg-secondary text-center px-2 text-white': true, 'bg-success': item.value == 'Open', 'bg-danger': (item.value == 'Closed' || item.value == 'Cancelled' || item.value == 'Closed and Locked'), 'bg-warning': item.value == 'Draft' }">{{
                                                            item.value }}</span>
                                                </p>
                                            </template>
                                            <p class="px-2 w-50 float-left">
                                                <i class="fa fa-dot-circle-o text-dark float-left pr-2"
                                                    style="font-size: 12pt;"></i>
                                                <span class="pl-1 font-weight-bold">CC Users: </span>
                                                <span>{{ ListOfCCUsers }}</span>
                                            </p>
                                        </div>
                                        <div v-if="isTicketText"
                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left panel1 mb-0 p-0"
                                            style="min-height:100px;max-height: 500px; overflow-x: hidden;">
                                            <pre v-html="TicketTextDetail" v-if="!isBlank(TicketTextDetail)"></pre>
                                            <div class="col-12 text-center text-danger" v-if="!isBlank(TicketTextDetail)">
                                                {{ $t('NoRecordfound') }}</div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props: {
        TicketId: {
            type: Number,
            required: false
        },
        TicketData: {
            type: Object,
            required: true,
        },
        TicketDetailDataForPopup: {
            type: Array,
            required: true,
        },
        ListOfCCUsers: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            UserType: null,
            isMoreTicketDetail: false,
            TicketTextDetail: '',
            isTicketText: false,
            isTicketDetail: false,
            LeftViewData: [],
            isLoading: false
        }

    },
    created() {
        var vm = this;
        vm.UserType = vm.GetUserInfo.UserType;
        debugger;

    },
    methods: {
        Close: function () {
            var vm = this;
            vm.isMoreTicketDetail = false;
        },
        showTicketDetail: function () {
            var vm = this;
            vm.isMoreTicketDetail = true;
            vm.isTicketDetail = true;
            vm.isTicketText = false;
            vm.LeftViewData = vm.TicketDetailDataForPopup;

        },
        openTicketTextTab: function () {
            var vm = this;
            debugger;
            vm.isMoreTicketDetail = true;
            vm.isTicketText = true;
            vm.isTicketDetail = false;
            var url = `TicketId=${this.TicketId}&formId=`;
            DataService.GetTicketText(url).then(function (response) {
                debugger;
                if (response.data != null && response.data.status == "Success") {
                    vm.TicketTextDetail = response.data.result.DATA[0].TICKETTEXT;
                }
            });
        },

    }
}
</script>